@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


button {
  padding: 10px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

.homePageContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: "Playfair Display", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

button.startbutton {
  top:17vh;
  margin: 14vh 2vh;
  padding: 7vh 2vh;
  border-radius: 15vh;
  background-color: #eeddc7;
  color: #61685f;
  font-size: 1.1em;
  border: 1px solid #6d655a;
}

button.startbutton:hover {
  background-color: #ebd3b3;
}

h1 {
  font-size: 1.5em;
}

p {
  margin-top: 6vh 0vh;
}


.goals-button {
    margin: 5px;
    padding: 10px;
    border: 1px solid #ddd;
    background-color: #657c62;
    cursor: pointer;
  }
  
  .goals-button.selected {
    background-color: #4caf50;
    color: white;
  }
  
  .goals-submit {
    background-color: #007bff;
    color: white;
  }
  
  .goals-submit:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
/* Product.css */
.product-container {
    text-align: center;
    margin-top: 20px;
  }
  
  .product-container button {
    padding: 10px 20px;
    border: 1px solid #ddd;
    background-color: #e7e7e7;
    color: #333;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
    font-size: 1.1em; /* Increase font size */
  }
  
  .product-container button.selected {
    background-color: #4caf50;
    color: white;
  }
  
  .product-container button.submit {
    background-color: #007bff;
    color: white;
  }
  
  .product-container button.submit:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .product-container p {
    margin: 10px;
    padding: 5px;
    background-color: #f0f0f0;
    border: 1px solid #ddd;
    border-radius: 5px;
    color: #666;
    transition: color 0.3s;
    font-size: 0.85em; /* Decrease font size */
  }
  
  .product-container .selected + p {
    color: #000;
  }
  
